.card {
	border-radius: 0.5rem;
	box-sizing: border-box;
	color: currentColor;
	overflow: hidden;
	padding: 1.25rem;
	position: relative;
}

.card::before {
	background-color: var(--color-card-background);
	content: '';
	height: 100%;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: -1;
}
